import { NgModule, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';
import { VendorComponent } from './vendor.component';
import{VendorRoutingModule} from './vendor-routing.module';
import{MatTableModule,MatSortModule,MatPaginatorModule,
  MatCardModule,MatInputModule, MatFormFieldModule, MatButtonModule, MatIconModule, MatTooltipModule, MatDialogModule, MatSelectModule, MatRadioModule} from '@angular/material'
import{FormsModule,ReactiveFormsModule} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import{HttpModule} from '@angular/http';
import { AddVendorComponent } from './add-vendor/add-vendor.component';
import { EditVendorComponent } from './edit-vendor/edit-vendor.component';
import { DeleteVendorComponent } from './delete-vendor/delete-vendor.component';
import { NotifierModule, NotifierOptions } from 'angular-notifier';
import { MasterService } from '../master/master.service';
import { FoundryModule } from '../foundry.module';
const customNotifierOptions: NotifierOptions = {
  position: {
    horizontal: {
      position: 'middle',
      distance: 12
    },
    vertical: {
      position: 'top',
      distance: 50,
      gap: 10
    }
  },
  theme: 'material',
  behaviour: {
    autoHide: 1000,
    onClick: 'hide',
    onMouseover: 'pauseAutoHide',
    showDismissButton: true,
    stacking: 4
  },
  animations: {
    enabled: true,
    show: {
      preset: 'slide',
      speed: 300,
      easing: 'ease'
    },
    hide: {
      preset: 'fade',
      speed: 300,
      easing: 'ease',
      offset: 50
    },
    shift: {
      speed: 300,
      easing: 'ease'
    },
    overlap: 10
  }
};

@NgModule({
  declarations: [VendorComponent, AddVendorComponent, EditVendorComponent, DeleteVendorComponent],
  imports: [
    CommonModule,VendorRoutingModule,MatTableModule,MatSortModule,MatPaginatorModule,MatCardModule,
    FormsModule,ReactiveFormsModule,MatInputModule,HttpClientModule,HttpModule,MatSelectModule,MatRadioModule,FoundryModule,
    MatFormFieldModule,MatButtonModule,MatIconModule,MatTooltipModule,MatDialogModule,NotifierModule.withConfig(customNotifierOptions)
  ],
  schemas:[CUSTOM_ELEMENTS_SCHEMA],
  entryComponents:[AddVendorComponent,EditVendorComponent,DeleteVendorComponent],
  providers:[MasterService]
})
export class VendorModule { }
