import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Vendor } from '../vendor';
import { VendorService } from '../vendor.service';
import { NotifierService } from 'angular-notifier';
import { MasterService } from '../../master/master.service';
import { Master } from '../../master/master';
import { FormBuilder, Validators } from '@angular/forms';
import { ValidationService } from '../../validation/validation-service';
import { VendorTypeMaster } from '../../master/vendor-type-master/vendor-type-master';

@Component({
  selector: 'app-edit-vendor',
  templateUrl: './edit-vendor.component.html',
  styleUrls: ['./edit-vendor.component.scss']
})
export class EditVendorComponent implements OnInit {
  userForm:any;
  public notifier: NotifierService;
  public masterList:Master[];
  public vendorTypeMasterList: VendorTypeMaster[];
  constructor(public matDialogRef: MatDialogRef<EditVendorComponent>,
    @Inject(MAT_DIALOG_DATA) public data: Vendor,
    public vendorService: VendorService, public notifierService: NotifierService,
    public masterService:MasterService, private formBuilder: FormBuilder) {
    this.notifier = notifierService;
    
    this.userForm = this.formBuilder.group({
      'vendorName': ['', [Validators.required, ValidationService.nameValidation]],
      'vendorType': ['', Validators.required],
      'mobile': ['', [Validators.required, ValidationService.mobileValidator]],
      'email': ['', [Validators.required, ValidationService.emailValidator]],
      'address': ['', [Validators.required]],
      'preferredType': ['', [Validators.required]],
    });
  }

  ngOnInit() {
    this.getVendorTypeMaster();
   }
   
  getVendorTypeMaster() {
    this.masterService.getVendorTypeMaster().subscribe((data) => {
      this.vendorTypeMasterList = data;
    }, (err) => {
      console.log(err);
    });
  }

  updateVendor() {
    this.data.modifiedBy=localStorage.getItem('user');
    this.vendorService.updateVendor(this.data).subscribe((data) => {
      this.notifier.notify('success', 'Updated Successfully');
      this.matDialogRef.close();
    }, (err) => {
      this.notifier.notify('error', 'Not Update');
    });
  }

  cancel() {
    this.matDialogRef.close();
  }
}
