import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { VendorService } from '../vendor.service';
import { NotifierService } from 'angular-notifier';

@Component({
  selector: 'app-delete-vendor',
  templateUrl: './delete-vendor.component.html',
  styleUrls: ['./delete-vendor.component.scss']
})
export class DeleteVendorComponent implements OnInit {
  private notifier: NotifierService;
  constructor(public matDialogRef: MatDialogRef<DeleteVendorComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public vendorService: VendorService, notifierService: NotifierService) {
    this.notifier = notifierService;
  }

  ngOnInit() {

  }

  deleteVendor() {
    this.data.modifiedBy=localStorage.getItem('user');
    this.vendorService.deleteVendor(this.data).subscribe((data) => {
      this.notifier.notify('success', 'Deleted Successfully');
      this.matDialogRef.close();
    }, (err) => {
      this.notifier.notify('error', 'Not Delete');
    })
  }

  cancel() {
    this.matDialogRef.close();
  }
}
