import { Component, OnInit, ViewChild } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Vendor } from './vendor';
import { MatTableDataSource, MatPaginator, MatSort } from '@angular/material';
import { MatDialogRef, MatDialog } from '@angular/material';
import { AddVendorComponent } from './add-vendor/add-vendor.component';
import { DeleteVendorComponent } from './delete-vendor/delete-vendor.component';
import { EditVendorComponent } from './edit-vendor/edit-vendor.component';
import { VendorService } from './vendor.service';
import { NotifierService } from 'angular-notifier';
import { MasterService } from '../master/master.service';
import { Master } from '../master/master';
@Component({
  selector: 'app-vendor',
  templateUrl: './vendor.component.html',
  styleUrls: ['./vendor.component.scss']
})
export class VendorComponent implements OnInit {
  public vendor: Vendor;
  public notifier: NotifierService;
  public masterList: Master[];
  displayedColumns: String[] = ['vendorName', 'vendorType', 'mobile', 'email', 'address', 'actions'];
  dataSource;
  constructor(public httpClient: HttpClient, public matDialog: MatDialog,
    public vendorService: VendorService, public notifierService: NotifierService,
    public masterService: MasterService) {
    this.notifier = notifierService;
  }
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  ngOnInit() {
    this.vendor = new Vendor();
    this.getVendorList();
  }

  applyFilter(filterValue: String) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  getVendorList() {
    this.vendorService.getAllVendors().subscribe((data) => {
      this.dataSource = new MatTableDataSource(data);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }, (err) => {
      console.log(err);
    });
  }

  addVendor() {
    const dialogRef = this.matDialog.open(AddVendorComponent, {
    });
    dialogRef.afterClosed().subscribe((reasult) => {
      this.getVendorList();
    });
  }

  deleteVendor(i: number, _id: String, vendorName: String, mobile: number, email: String) {
    const dialogRef = this.matDialog.open(DeleteVendorComponent, {
      data: { _id: _id, vendorName: vendorName, mobile: mobile, email: email }
    });
    dialogRef.afterClosed().subscribe((reasult) => {
      this.getVendorList();
    });
  }

  editVendor(i: number, _id: String, vendorName: String, vendorType: String,
    mobile: number, email: String, address: String, preferredType: String) {
    const dialogRef = this.matDialog.open(EditVendorComponent, {
      data: {
        _id: _id, vendorName: vendorName, vendorType: vendorType,
        mobile: mobile, email: email, address: address, preferredType: preferredType
      }
    });
    dialogRef.afterClosed().subscribe((data) => {
      this.getVendorList();
    });
  }

  refresh() {
    this.getVendorList();
  }
}
